@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Nunito', sans-serif;
  font-size: 20px;
  letter-spacing: 1px;
}

#main-page{
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background: rgb(148,52,182);
  background: -webkit-gradient(linear, left top, right top, from(rgba(148,52,182,1)), to(rgba(134,16,201,1)));
  background: linear-gradient(90deg, rgba(148,52,182,1) 0%, rgba(134,16,201,1) 100%);
}

nav{
  top: 0; margin: 0; padding: 0;
  position: fixed;
  width: 100%;
  height: 30px;
  padding-top: 5px;
  background-color: rgb(0,0,0 , 0.5);
}

.nav-list{
  float: right;
  margin: 0;
}

.nav-item{
  display: inline-block;
}

.nav-item a{
  padding: 0 20px 10px;
  height: 30px;
  color: aliceblue;
  text-decoration: none;
}

.nav-item a:hover{
  border-bottom: 1px solid aliceblue;
}
